import { GitHubCircle, ArrowRight } from "../UI/icons";
import { ProjImage, ProjTitle, ProjDate, ProjDesc } from "./utils";
import "./css/projStyles.css";

const getProjTitle = (demoUrl, title) => {
    return demoUrl !== "" ? (
        <span className="flex flex-row hover:text-main space-x-2">
            <a
                className="z-20"
                href={demoUrl}
                rel="noopener noreferrer"
                target="_blank"
                id="otherProjHyperLink"
            >
                {title}
            </a>
            <p className="font-normal hidden xxxs:block ml-1 my-auto transform -rotate-45 group-hover:translate-x-1 group-hover:-translate-y-1">
                <ArrowRight customClasses="w-4" />
            </p>
        </span>
    ) : (
        <p>{title}</p>
    );
};

const DevProjCard = (props) => {
    return (
        <div
            className={`h-full w-full relative group flex flex-col ${
                props.classes
            } ${props.hidden ? `hidden` : ``}`}
        >
            {props.proj.url.img !== undefined ? (
                <ProjImage
                    customClasses="group-hover:opacity-80"
                    imgUrl={props.proj.url.img}
                    imgAltText="dev-project"
                    width="545"
                    height="316"
                />
            ) : null}

            <header
                className={
                    "flex flex-col" + (props.headerGrow ? " flex-grow" : "")
                }
            >
                <ProjTitle>
                    <span className="flex justify-between">
                        {getProjTitle(props.proj.url.demo, props.proj.title)}
                        <a
                            href={props.proj.url.code}
                            rel="noopener noreferrer"
                            target="_blank"
                            className="text-submain-color hover:text-main z-30"
                        >
                            <GitHubCircle customClasses="w-5 xxxs:w-6 sm:w-7 md:w-6 lg:w-7" />
                        </a>
                    </span>
                </ProjTitle>
                <ProjDate>{props.proj.date}</ProjDate>
                <ProjDesc>{props.proj.desc}</ProjDesc>
            </header>

            <footer className="submain mt-5 lg:mt-6 flex flex-nowrap space-x-3 xxs:space-x-5 overflow-x-auto overscroll-x-none z-30">
                {props.proj.tags.map((tag) => (
                    <span key={tag} className="flex-none mb-1">
                        {tag}
                    </span>
                ))}
            </footer>
        </div>
    );
};

export default DevProjCard;
