import { useState } from "react";
import { Spinner } from "../UI/spinner";

export const ProjImage = (props) => {
    const [loaded, setLoaded] = useState(false);

    return (
        <span className="relative mb-3 xs:mb-4 lg:mb-5">
            <img
                onLoad={() => setLoaded(true)}
                className={`w-full h-auto rounded-sm xxs:rounded-md lg:rounded-lg ${props.customClasses}`}
                src={props.imgUrl}
                width={props.width}
                height={props.height}
                alt={props.imgAltText}
                loading="lazy"
            />
            <div
                className={`${
                    loaded ? `hidden` : ``
                } absolute top-0 bottom-0 left-0 right-0 bg-secondary bg-opacity-20`}
            >
                <Spinner />
            </div>
        </span>
    );
};

export const ProjTitle = (props) => (
    <span
        className={`heading lg:text-xl mb-1 sm:mb-2 md:mb-1 lg:mb-2 ${
            props.customClasses ? props.customClasses : ``
        }`}
    >
        {props.children}
    </span>
);

export const ProjDate = (props) => (
    <p
        className={`submain mb-3 sm:mb-4 md:mb-3 lg:mb-5 ${
            props.customClasses ? props.customClasses : ``
        }`}
    >
        {props.children}
    </p>
);

export const ProjDesc = (props) => (
    <p
        className={`content text-xs-sm xxs:text-sm-base md:text-xs-sm lg:leading-7 lg:tracking-wide lg:text-base ${props.customClasses}`}
    >
        {props.children}
    </p>
);

export const ExpandProjList = (props) => {
    return (
        <div className="flex items-center justify-center mt-6 xs:mt-8 md:mt-10 lg:mt-12">
            <button
                aria-label="Show/Hide extra projects"
                onClick={() => {
                    if (props.totalOpenCards < props.projLen)
                        props.setTotalOpenCards(props.totalOpenCards + 2);
                    else props.setTotalOpenCards(props.initialCardToDisplay);
                }}
                className="text-main px-5 lg:px-6 py-3 box-border-md"
            >
                <span className="font-manrope font-bold text-xs lg:text-sm">
                    {props.totalOpenCards < props.projLen ? (
                        <p>
                            Show&nbsp;&nbsp;
                            {props.projLen - props.totalOpenCards}
                            &nbsp;&nbsp;More
                        </p>
                    ) : (
                        <p>Show Less</p>
                    )}
                </span>
            </button>
        </div>
    );
};
