import * as actionTypes from "./actionTypes";
import { firebaseAxiosInstance } from "../../base-axios";
import LocalStorage from "../../utils/localStorage";

const fetchProjFailed = (errObj) => {
    return {
        type: actionTypes.FETCH_PROJ_FAILED,
        error: errObj,
    };
};

// -------------------------------------------------------------------
// ------------------- fetch featured projects data -----------------
// -------------------------------------------------------------------
const fetchFeatProj = (proj) => {
    return {
        type: actionTypes.FETCH_FEAT_PROJ,
        proj: proj,
    };
};
export const initFeatProj = () => (dispatch) => {
    const prevState = LocalStorage.get("featuredProjects");
    if (prevState !== null) return dispatch(fetchFeatProj(prevState));

    firebaseAxiosInstance
        .get("/featuredProjects.json")
        .then((res) => {
            res.data.reverse();
            dispatch(fetchFeatProj(res.data));
            LocalStorage.set("featuredProjects", res.data);
        })
        .catch((err) => {
            const errObj = {
                type: "featProjErrMsg",
                msg: err.toString(),
            };
            dispatch(fetchProjFailed(errObj));
        });
};

