import { Fade } from "react-awesome-reveal";
import { useState } from "react";
import { connect } from "react-redux";

import { HeadLine, PrimaryContainer, ErrorContainer } from "../UI/utils";
import { ExpandProjList } from "./utils";
import { Spinner } from "../UI/spinner";
import DevProjCard from "./devProjCard";

const OtherProjects = (props) => {
    // state hook
    const [totalOpenCards, setTotalOpenCards] = useState(4);

    let sectionContent = <Spinner />;

    if (props.errMsg !== "") {
        sectionContent = <ErrorContainer>{props.errMsg}</ErrorContainer>;
    }
    // if other projects is not null then only exec
    else if (props.otherProj !== null) {
        sectionContent = (
            <>
                <div className="grid gap-4 mx-auto w-auto xs:w-11/12 sm:w-10/12 md:w-auto grid-cols-1 md:grid-cols-2">
                    <Fade cascade triggerOnce>
                    {props.otherProj.map((proj, index) => (
                        <DevProjCard
                            key={index}
                            hidden={index < totalOpenCards ? false : true}
                            classes="bg-secondary-color p-4 xxs:p-6 lg:p-8 rounded-md lg:rounded-lg"
                            headerGrow
                            proj={proj}
                        />
                    ))}
                    </Fade>
                </div>
                <ExpandProjList
                    totalOpenCards={totalOpenCards}
                    setTotalOpenCards={setTotalOpenCards}
                    initialCardToDisplay={4}
                    projLen={props.otherProj.length}
                />
            </>
        );
    }

    return (
        <PrimaryContainer>
            <HeadLine>Other Noteworthy Projects.</HeadLine>
            {sectionContent}
        </PrimaryContainer>
    );
};

// subscribe to the global redux store
const mapStateToProps = (state) => {
    return {
        otherProj: state.projects.otherProj,
        errMsg: state.projects.errorMsgs.otherProjErrMsg,
    };
};

export default connect(mapStateToProps, null)(OtherProjects);
