import SecureLocalStorage from "./secureLocalStorage";

const SecretKey = "dhyey-thumar-website";
const TTL_ms = 24 * 60 * 60 * 1000; // expires in 24 hrs

const LocalStorage = {
    get: (LSKey) => {
        const storedData = JSON.parse(window.localStorage.getItem(LSKey));
        if (storedData === null || storedData === undefined) return null;

        // check the expiry data of localstorage
        if (
            storedData.expires_at !== null &&
            storedData.expires_at < new Date().getTime()
        ) {
            window.localStorage.removeItem(LSKey);
            return null;
        }

        // decrypt the localstorage data
        return SecureLocalStorage.decrypt(storedData.encryptedData, SecretKey);
    },

    set: (LSKey, data) => {
        const storeData = {
            encryptedData: SecureLocalStorage.encrypt(data, SecretKey),
            expires_at: new Date().getTime() + TTL_ms / 1,
        };
        window.localStorage.setItem(LSKey, JSON.stringify(storeData));
    },
};

export default LocalStorage;
