import * as Aes from "crypto-js/aes";
import * as CryptoJsCore from "crypto-js/core";
import stringify from "json-stringify-safe";

const SecureLocalStorage = {
    encrypt: (dataToEncrypt, secretKey) => {
        return Aes.encrypt(stringify(dataToEncrypt), secretKey).toString();
    },

    decrypt: (dataToDecrypt, secretKey) => {
        try {
            const decryptedString = Aes.decrypt(
                dataToDecrypt,
                secretKey
            ).toString(CryptoJsCore.enc.Utf8);
            return JSON.parse(decryptedString);
        } catch (err) {
            console.error(
                `LocalStorage decryption gave the following error :: ${err}`
            );
            return null;
        }
    },
};

export default SecureLocalStorage;
