import * as actionTypes from "./actionTypes";
import { firebaseAxiosInstance } from "../../base-axios";
import LocalStorage from "../../utils/localStorage";

const fetchCommonFailed = (errMsg) => {
    return {
        type: actionTypes.FETCH_COMMON_FAILED,
        error: errMsg,
    };
};

// -------------------------------------------------------------------
// ---------------------- fetch the common data ---------------------
// -------------------------------------------------------------------
const fetchCommonData = (commonData) => {
    return {
        type: actionTypes.FETCH_COMMON_DATA,
        commonData: commonData,
    };
};
export const initCommonData = () => (dispatch) => {
    const prevState = LocalStorage.get("commonData");
    if (prevState !== null) return dispatch(fetchCommonData(prevState));

    firebaseAxiosInstance
        .get("/commonData.json")
        .then((res) => {
            dispatch(fetchCommonData(res.data));
            LocalStorage.set("commonData", res.data);
        })
        .catch((err) => {
            dispatch(fetchCommonFailed(err.toString()));
        });
};
