import { Fade } from "react-awesome-reveal";
import { connect } from "react-redux";

import { HeadLine, PrimaryContainer, ErrorContainer } from "../UI/utils";
import { Spinner } from "../UI/spinner";
import DevProjCard from "./devProjCard";

const FeaturedProjects = (props) => {
    let sectionContent = <Spinner />;

    if (props.errMsg !== "") {
        sectionContent = <ErrorContainer>{props.errMsg}</ErrorContainer>;
    }
    // if feat projects is not null then only exec
    else if (props.featProj !== null) {
        sectionContent = (
            <div className="grid gap-10 mx-auto w-auto xs:w-11/12 sm:w-10/12 md:w-auto grid-cols-1 md:grid-cols-2">
                <Fade cascade triggerOnce>
                    {props.featProj.map((proj, index) => (
                        <DevProjCard key={index} classes="" proj={proj} />
                    ))}
                </Fade>
            </div>
        );
    }

    return (
        <div className="pt-16 xl:pt-24">
            <PrimaryContainer>
                <HeadLine>Featured Projects.</HeadLine>
                {sectionContent}
            </PrimaryContainer>
        </div>
    );
};

// subscribe to the global redux store
const mapStateToProps = (state) => {
    return {
        featProj: state.projects.featProj,
        errMsg: state.projects.errorMsgs.featProjErrMsg,
    };
};

export default connect(mapStateToProps, null)(FeaturedProjects);
