import { Fade } from "react-awesome-reveal";
import { useState } from "react";
import { connect } from "react-redux";

import { HeadLine, SecondaryContainer, ErrorContainer } from "../UI/utils";
import {
    ProjImage,
    ProjTitle,
    ProjDate,
    ProjDesc,
    ExpandProjList,
} from "./utils";
import { Spinner } from "../UI/spinner";

const DesignProjects = (props) => {
    // state hook
    const [totalOpenCards, setTotalOpenCards] = useState(3);

    let sectionContent = <Spinner />;

    if (props.errMsg !== "") {
        sectionContent = <ErrorContainer>{props.errMsg}</ErrorContainer>;
    }
    // if design projects is not null then only exec
    else if (props.designProj !== null) {
        sectionContent = (
            <>
                <div className="grid gap-6 xxxs:gap-8 lg:gap-10 mx-auto w-11/12 xxxs:w-10/12 xxs:w-9/12 xs:w-auto lg:w-11/12 grid-cols-1 xs:grid-cols-2 md:grid-cols-3">
                    <Fade cascade triggerOnce>
                        {props.designProj.map((proj, index) => {
                            return (
                                <div
                                    key={index}
                                    className={
                                        "h-auto w-full flex flex-col " +
                                        (index < totalOpenCards ? "" : "hidden")
                                    }
                                >
                                    <ProjImage
                                        imgUrl={proj.imageUrl}
                                        imgAltText="design-project"
                                        width="320"
                                        height="320"
                                    />
                                    <header className="text-content-color flex flex-col">
                                        <ProjTitle>{proj.title}</ProjTitle>
                                        <ProjDate>
                                            {proj.date}
                                            &nbsp;&nbsp;&bull;&nbsp;&nbsp;
                                            {proj.tag}
                                        </ProjDate>
                                        <ProjDesc>{proj.desc}</ProjDesc>
                                    </header>
                                </div>
                            );
                        })}
                    </Fade>
                </div>
                <ExpandProjList
                    totalOpenCards={totalOpenCards}
                    setTotalOpenCards={setTotalOpenCards}
                    initialCardToDisplay={3}
                    projLen={props.designProj.length}
                />
            </>
        );
    }

    return (
        <SecondaryContainer>
            <HeadLine>
                Design Projects.
                <p className="submain pt-1">
                    (All the project are designed using Adobe xd)
                </p>
            </HeadLine>
            {sectionContent}
        </SecondaryContainer>
    );
};

// subscribe to the global redux store
const mapStateToProps = (state) => {
    return {
        designProj: state.projects.designProj,
        errMsg: state.projects.errorMsgs.designProjErrMsg,
    };
};

export default connect(mapStateToProps, null)(DesignProjects);
